.btn{
 margin-top: 60px;
 gap: 20px;
@media screen and (min-width: $tablet) {
    margin-top: 40px;
    gap: 32px;
}
@media screen and (min-width: $desktop) {
    gap: 16px;
}
}

.button{
    width: 150px;
    height: 44px;
    border-radius: 5px;
    border: 1px solid $white;
    color: $white;
    background: rgba(0, 0, 0, 0);
    cursor: pointer;
    &:hover,
    &:focus,
    &:active {
    background-color: $orange-button;
    box-shadow: 0px 8px 43px $box-shadow-color;
    border: none;
    }
}

